import React, {
	useReducer,
	useContext,
	createContext,
	Dispatch,
	useEffect,
} from 'react';
import { THEME, SNOW } from '../constants';
import * as Dom from '../utils/dom';

function canUseDOM() {
	return !!(
		typeof window !== 'undefined' &&
		window.document &&
		window.document.createElement
	);
}

function toggleTheme(theme) {
	switch (theme) {
		case THEME.LIGHT: {
			Dom.addClassToBody(THEME.LIGHT);
			Dom.removeClassToBody(THEME.DARK);
			break;
		}
		case THEME.DARK: {
			Dom.addClassToBody(THEME.DARK);
			Dom.removeClassToBody(THEME.LIGHT);
			break;
		}
	}
}

// íìí íìë¤ì ë¯¸ë¦¬ ì ì¸
type Theme = string;
type Snow = string;

// ìíë¥¼ ìí íì
type State = {
	theme: Theme;
	snow: Snow;
};

// ëª¨ë  ì¡ìë¤ì ìí íì
type Action =
	| { type: 'SET_THEME'; theme: Theme }
	| { type: 'SET_SNOW'; snow: Snow };

// ëì¤í¨ì¹ë¥¼ ìí íì (Dispatch ë¥¼ ë¦¬ì¡í¸ìì ë¶ë¬ì¬ ì ìì), ì¡ìë¤ì íìì Dispatch ì Genericsë¡ ì¤ì 
type ThemeDispatch = Dispatch<Action>;

// Context ë§ë¤ê¸°
const ThemeStateContext = createContext<State | null>(null);
const ThemeDispatchContext = createContext<ThemeDispatch | null>(null);

// ë¦¬ëì
function reducer(state: State, action: Action): State {
	switch (action.type) {
		case 'SET_THEME':
			localStorage.setItem('theme', action.theme);
			document.documentElement.setAttribute('data-theme', action.theme);
			toggleTheme(action.theme);
			return {
				...state,
				theme: action.theme,
			};
		case 'SET_SNOW':
			localStorage.setItem('snow', action.snow);
			return {
				...state,
				snow: action.snow,
			};
		default:
			throw new Error('Unhandled action');
	}
}

// ThemeProvider ìì useReduerë¥¼ ì¬ì©íê³ 
// ThemeStateContext.Provider ì ThemeDispatchContext.Provider ë¡ children ì ê°ì¸ì ë°íí©ëë¤.
export function ThemeProvider({ children }: { children: React.ReactNode }) {
	let localStorageTheme = THEME.LIGHT;
	let localStorageSnow = SNOW.OFF;
	useEffect(() => {
		localStorageTheme = localStorage.getItem('theme');
		if (localStorageTheme === THEME.DARK) {
			document.documentElement.setAttribute('data-theme', THEME.DARK);
			localStorage.setItem('theme', THEME.DARK);
			localStorageTheme = THEME.DARK;
		} else {
			document.documentElement.setAttribute('data-theme', THEME.LIGHT);
			localStorage.setItem('theme', THEME.LIGHT);
			localStorageTheme = THEME.LIGHT;
		}
		localStorageSnow = localStorage.getItem('snow');
		if (localStorageSnow === SNOW.ON) {
			localStorage.setItem('snow', SNOW.ON);
			localStorageSnow = SNOW.ON;
		} else {
			localStorage.setItem('snow', SNOW.OFF);
			localStorageSnow = SNOW.OFF;
		}

		const setTheme = () =>
			dispatch({
				type: 'SET_THEME',
				theme: localStorageTheme,
			});
		const setSnow = () =>
			dispatch({
				type: 'SET_SNOW',
				snow: localStorageSnow,
			});
		setTheme();
		setSnow();
	}, []);

	const [state, dispatch] = useReducer(reducer, {
		theme: localStorageTheme as Theme,
		snow: localStorageSnow as Theme,
	});

	return (
		<ThemeStateContext.Provider value={state}>
			<ThemeDispatchContext.Provider value={dispatch}>
				{children}
			</ThemeDispatchContext.Provider>
		</ThemeStateContext.Provider>
	);
}

// state ì dispatch ë¥¼ ì½ê² ì¬ì©íê¸° ìí ì»¤ì¤í Hooks
export function useThemeState() {
	const state = useContext(ThemeStateContext);
	if (!state) throw new Error('Cannot find ThemeProvider'); // ì í¨íì§ ììë ìë¬ë¥¼ ë°ì
	return state;
}

export function useThemeDispatch() {
	const dispatch = useContext(ThemeDispatchContext);
	if (!dispatch) throw new Error('Cannot find ThemeProvider'); // ì í¨íì§ ììë ìë¬ë¥¼ ë°ì
	return dispatch;
}
